import XmlEpubRead from "./xmlComponents/xmlEpubRead/src/xmlEpubRead.vue";
import XmlEpubReadPc from "./xmlComponents/xmlEpubRead/src/xmlEpubReadPc.vue";
const components = [XmlEpubRead, XmlEpubReadPc];
//  install  Vue  use 
const install: any = (Vue: any) => {
  // 
  if (install.installed) return;
  // 
  components.map((component) => Vue.component(component.name, component));
};
// 
if (typeof window !== "undefined" && (window as any).Vue) {
  install((window as any).Vue);
}
export default {
  //  install Vue.use() 
  install,
  // 
  XmlEpubRead,
  XmlEpubReadPc,
};
